import ProjectCard from "./ProjectCard";

const Projects = ({ setIsHovering }) => {
    const delay = 500;
    return (
        <div className="panel">
            <div className="projects-container">
                <h1>Projects</h1>
                <div className="project-grid">
                    <ProjectCard 
                        setIsHovering={setIsHovering} 
                        image={'https://assets.stickpng.com/images/580b57fcd9996e24bc43c53e.png'} 
                        title={'Project Title A'}
                        description={'Project Descrption Lorem Ipsum Dolor It'}
                        delay={delay*0}
                    /> 
                    <ProjectCard 
                        setIsHovering={setIsHovering} 
                        image={'https://purepng.com/public/uploads/thumbnail//google-stadia-logo-hd4.png'} 
                        title={'Project Title B'}
                        description={'Project Descrption Lorem Ipsum Dolor It'}
                        delay={delay*1}
                    /> 
                    <ProjectCard 
                        setIsHovering={setIsHovering} 
                        image={'https://upload.wikimedia.org/wikipedia/commons/a/ab/Apple-logo.png'} 
                        title={'Project Title C'}
                        description={'Project Descrption Lorem Ipsum Dolor It'}
                        delay={delay*2}
                    />
                </div>
            </div>
        </div> 
    );
}
 
export default Projects;